.color__block {
  position: relative;
  height: 100%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  background: #639;
}
.color__text {
  position: absolute;
  bottom: 60px;
  width: 100%;
  text-align: center;
}
.color__value {
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 400;
  color: inherit;
}
.color__block--light {
  color: #fff;
}
.color__block--primary .color__value {
  font-size: 28px;
}



